:root {
  --selection-color: null;
  --white: #fbfaf8;
  --white-half-alpha: #fbfaf892;
  --white-quarter-alpha: #fbfaf843;
  --white-low-alpha: #fbfaf815;
  --dark: #060604;
  --dark-low-alpha: #0606042c;
  --accent-color: #bb377d;
  --highlight-color: #dfb669;
  --predefined-bookmark: #6856347e;
  --custom-bookmark: #3535357e;
  --add-bookmark: #4d685a7e;
  --settings: #4d685a7e;
  --primary-color: #faf7f0;
  --secondary-color: #e7decd;
  --blur-one-em: blur(1em);
  --blur-one-px: blur(1px);
  --blur-half-em: blur(.5em);
  --cross-display: none;
}

::selection {
  background: var(--selection-color);
}

#canvasFar {
  z-index: 0;
  background-image: linear-gradient(#ffffff40 0%, #ebebff00 60%);
  display: none;
  position: fixed;
  top: 0;
}

.disable-select {
  -webkit-user-select: none;
  user-select: none;
}

* {
  caret-color: #0000;
  scroll-behavior: smooth;
  outline-color: var(--selection-color);
  outline-offset: -1px;
  outline-width: 1px;
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
}

body {
  background-color: #282828;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 100svh;
  font-family: Ubuntu Mono, monospace;
  font-size: 12px;
  display: flex;
}

.justifySpaceBetween {
  justify-content: space-between !important;
}

#overlay {
  z-index: 0;
  cursor: auto;
  background-color: #00000060;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: block;
  position: fixed;
  inset: 0;
}

#gradient_overlay {
  opacity: 0;
  z-index: 0;
  cursor: auto;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.header {
  color: var(--primary-color);
  z-index: 13;
  background-color: #2684e15c;
  justify-content: space-between;
  gap: .25em;
  width: 100%;
  height: 3em;
  padding: .5em;
  font-size: .75em;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.header-left-block {
  align-items: center;
  gap: 1em;
  display: flex;
}

.user-avatar {
  border-radius: 50%;
  width: 2em;
  height: 2em;
}

.user-name {
  text-align: center;
}

.header-right-block {
  align-items: center;
  gap: 1em;
  display: flex;
}

#wrap {
  opacity: 0;
  z-index: 2;
  width: 100%;
  max-width: 1280px;
  transition: all .125s;
  display: block;
}

body::-webkit-scrollbar {
  -ms-overflow-style: none;
  display: none;
}

.flex-main-container-vertical {
  flex-direction: column;
  margin: auto;
  display: flex;
}

#home-widget-container {
  color: var(--primary-color);
  cursor: default;
  text-align: center;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 1em;
  padding-inline: 0;
  font-size: 2em;
  display: flex;
  top: 0;
  left: 0;
}

.home-widget {
  flex-direction: column;
  max-width: 90%;
  display: flex;
}

#main-heading-slider {
  scroll-snap-type: x mandatory;
  max-width: 100%;
  padding-block-end: .125em;
  transition: all .5s;
  display: flex;
  overflow: scroll hidden;
}

#main-heading-slider * {
  scroll-snap-align: center;
  scroll-snap-stop: always;
  flex: 1;
  min-width: 100%;
  margin-bottom: -.25em;
}

#main-heading-slider::-webkit-scrollbar {
  height: 8px;
  display: none;
}

#main-heading-slider::-webkit-scrollbar-track {
  border-radius: 10px;
  box-shadow: inset 0 0 5px gray;
}

#main-heading-slider::-webkit-scrollbar-thumb {
  background: var(--accent-color);
  border-radius: 10px;
}

.default-text-style {
  color: var(--primary-color);
  font-family: Ubuntu;
  font-size: 2em;
  line-height: 2em;
  rotate: none;
  text-shadow: 0 0 0 var(--accent-color), 0 0 0 #000 !important;
}

.brush-text-style-1 {
  color: #f5f5f5;
  text-shadow: 4px 4px 0 var(--accent-color), 6px 6px 0 #000;
  font-family: Vujahday Script, handwriting;
  font-size: 2.25em;
  line-height: 1.5em;
  rotate: -6deg;
}

.brush-text-style-2 {
  color: #f5f5f5;
  font-family: Advent Pro, sans-serif;
  font-size: 2.25em;
  line-height: 1.9em;
}

.brush-text-style-3 {
  color: #f5f5f5;
  text-shadow: 4px 4px 0 var(--accent-color), 6px 6px 0 #000;
  font-family: Nanum Brush Script, handwriting;
  font-size: 2.8em;
  line-height: 1.375em;
  rotate: none;
}

.brush-text-style-4 {
  color: #f5f5f5;
  text-shadow: 1px 1px #000;
  font-family: Are You Serious, handwriting;
  font-size: 2.5em;
  line-height: 1.5em;
  rotate: none;
}

.brush-text-style-5 {
  color: #f5f5f5;
  text-shadow: 1px 1px #000;
  font-family: Alkatra, display;
  font-size: 2.25em;
  line-height: 1.75em;
  rotate: none;
}

.brush-text-style-6 {
  color: #f5f5f5;
  text-shadow: 1px 1px #000;
  font-family: Montez, handwriting;
  font-size: 2.5em;
  line-height: 1.5em;
  rotate: none;
}

.separator {
  opacity: 0;
}

.separator-visible {
  opacity: 1;
}

.nowrap {
  text-wrap: nowrap;
}

.arrows {
  opacity: .85;
  z-index: 5;
  text-shadow: 0 2px #000;
  height: 0;
  font-size: 1em;
  transition: all .25s;
  overflow: hidden;
}

.arrows:hover {
  opacity: 1;
}

.arrows:active {
  opacity: .5;
  text-shadow: 0 0 #000;
}

#subtitle {
  font-family: Ubuntu;
  font-size: .65em;
}

.search {
  text-align: center;
  cursor: default;
  z-index: 2;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  width: 100%;
  height: 3em;
  margin-bottom: 2em;
  display: flex;
  position: relative;
}

.searchBarFocusMode {
  opacity: 0;
  -webkit-backdrop-filter: blur(.5em);
  backdrop-filter: blur(.5em);
  background-color: #ffffff1f;
  width: 100%;
  height: 100%;
  transition: all .2s;
  display: none;
  position: fixed;
  top: 0;
}

.searchContainer {
  z-index: 2;
  border-radius: 1.5em;
  order: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 4em;
  max-width: 44em;
  height: 3em;
  margin-inline: 1.85em;
  display: flex;
  overflow: hidden;
}

.searchContainer:has(.searchTerm:focus) {
  border-radius: 1em;
}

.searchTerm {
  background: none;
  border: 0;
  outline: 0;
  width: 100%;
  height: 100%;
  padding: 0 2em 0 1em;
  font-size: 1em;
}

.autofillContainer {
  background-color: #0000;
  justify-content: flex-start;
  align-items: center;
  width: calc(100% - 3.5em);
  min-width: 4em;
  max-width: 44em;
  height: 0;
  padding-inline: .1em;
  transition: all .125s;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.autofillItem {
  text-wrap: nowrap;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: start;
  z-index: 2;
  border-radius: .5em;
  width: 100%;
  min-width: 4em;
  max-width: 44em;
  min-height: 2.25em;
  max-height: 2.25em;
  margin-block-start: .25em;
  padding-block-start: .5em;
  padding-inline: 1em;
  font-family: Ubuntu;
  font-weight: 100;
  overflow: hidden;
  box-shadow: 0 2px 2px #0000002e;
  border: 1px solid #0000 !important;
}

input::-webkit-search-cancel-button {
  opacity: .5;
  margin-inline: .7em -1.25em;
  transition: all .2s;
}

input::-webkit-search-cancel-button:hover {
  opacity: .75;
}

input::-webkit-search-cancel-button:hover {
  scale: 1.1;
}

.searchButton {
  text-align: center;
  cursor: pointer;
  background: none;
  border: 0;
  outline: 0;
  width: 4em;
  height: 100%;
  font-size: 1em;
}

.searchButton > i {
  opacity: .5;
  background-color: #0000;
  background-size: 2px;
  border-radius: 2em;
  padding: .1em;
  transition: all .125s;
}

.searchButton:active > i {
  background-color: var(--selection-color);
  padding: .7em;
  color: var(--dark);
  padding: .8em;
  transition: all .2s;
}

.searchbox-style-glass {
  -webkit-backdrop-filter: var(--blur-one-em);
  backdrop-filter: var(--blur-one-em);
  background: var(--white-quarter-alpha);
  color: #fffd;
  border: 1px solid #ffffff38;
  transition: all .125s;
}

.searchbox-style-glass > :first-child::placeholder {
  color: #ffffff86;
}

.searchbox-style-glass > :first-child {
  color: #ffffff92;
  caret-color: #fff;
  transition: all .125s;
}

.searchbox-style-glass > :nth-child(2) > i, .searchbox-style-glass > :first-child:focus {
  color: #fff;
}

.searchbox-style-glass:hover {
  color: #fff;
  border: 1px solid #ffffffce;
}

.searchbox-style-glass:hover > :first-child {
  color: #fff;
}

.searchbox-style-glass:hover > .searchButton > i {
  opacity: 1;
}

.searchbox-style-glass:has(.searchTerm:focus) {
  box-shadow: 0 0 4px 0px var(--selection-color);
  border: 1px solid #fff0;
}

.searchbox-style-glass > :first-child::-webkit-search-cancel-button {
  filter: grayscale() invert() brightness(200%);
}

.searchbox-style-light {
  color: #000d;
  background: #fff;
  border: 1px solid #fff;
  transition: all .125s;
}

.searchbox-style-light > :first-child::placeholder {
  color: #75757586;
}

.searchbox-style-light > :first-child {
  color: #00000092;
  caret-color: #000;
  transition: all .125s;
}

.searchbox-style-light > :nth-child(2) > i, .searchbox-style-light > :first-child:focus {
  color: #000;
}

.searchbox-style-light:hover {
  border: 1px solid var(--selection-color);
  color: #000;
}

.searchbox-style-light:hover > :first-child {
  color: #000;
}

.searchbox-style-light:hover > .searchButton > i {
  opacity: 1;
}

.searchbox-style-light:has(.searchTerm:focus) {
  box-shadow: 0 0 4px 0px var(--selection-color);
  border: 1px solid #0000;
}

.searchbox-style-light > :first-child::-webkit-search-cancel-button {
  filter: grayscale() invert() brightness(50%);
}

.searchbox-style-dark {
  color: #fffd;
  background: #000;
  border: 1px solid #ffffff1e;
  transition: all .125s;
}

.searchbox-style-dark > :first-child::placeholder {
  color: #75757586;
}

.searchbox-style-dark > :first-child {
  caret-color: #fff;
  color: #ffffff92;
  transition: all .125s;
}

.searchbox-style-dark > :nth-child(2) > i, .searchbox-style-dark > :first-child:focus {
  color: #fff;
}

.searchbox-style-dark:hover {
  border: 1px solid var(--selection-color);
  color: #fff;
}

.searchbox-style-dark:hover > :first-child {
  color: #fff;
}

.searchbox-style-dark:hover > .searchButton > i {
  opacity: 1;
}

.searchbox-style-dark:has(.searchTerm:focus) {
  box-shadow: 0 0 4px 0px var(--selection-color);
  border: 1px solid #fff0;
}

.searchbox-style-dark > :first-child::-webkit-search-cancel-button {
  filter: grayscale() invert() brightness(200%);
}

.searchTerm:focus + .searchButton > i {
  opacity: 1;
}

.flex-sub-container-horizontal {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
  padding: 0 .5em;
  display: flex;
}

.flex-sub-container-horizontal > div {
  color: var(--primary-color);
  cursor: pointer;
  caret-color: #0000;
  border: 1px solid #ffffff38;
  border-radius: 50%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 4em;
  height: 4em;
  margin: 2%;
  text-decoration: none;
  display: flex;
}

.predefined-bookmark {
  background-color: var(--predefined-bookmark);
  -webkit-backdrop-filter: var(--blur-one-em);
  backdrop-filter: var(--blur-one-em);
}

.flex-sub-container-horizontal > div > i {
  font-size: 2em;
}

#add_bookmark_button:focus, #add_bookmark_button:hover, #settings_button:focus, #settings_button:hover, .flex-sub-container-horizontal > :focus, .flex-sub-container-horizontal > :hover {
  background-color: var(--highlight-color);
  transition: all .5s;
}

.flex-wallpaper-horizontal {
  z-index: 2;
  flex-flow: row;
  justify-content: start;
  width: 100%;
  max-height: 10em;
  padding: 0 0 .75em;
  display: none;
  position: fixed;
  bottom: 0;
  overflow-x: auto;
}

.thumb-group {
  cursor: pointer;
  border-radius: 1em;
  width: 8em;
  height: 4.8em;
  margin: .5em;
}

.advanced-settings-button {
  cursor: pointer;
  z-index: 3;
  background-color: #fff;
  border-radius: 0 1.5em 1.5em 0;
  justify-content: center;
  align-items: center;
  min-width: 9em;
  margin: 1em 0;
  padding: 0;
  padding-block: .5em;
  padding-inline: 1em;
  font-size: .5em;
  display: flex;
  position: sticky;
  left: 0;
  transform: translateX(-10em);
}

.advanced-settings-button:hover > i {
  transform: scale(1.125);
}

.advanced-settings-button:active > i {
  opacity: .5;
  transform: scale(1);
}

.advanced-settings-button > i {
  color: #060604;
  opacity: .8;
  font-size: 4em;
  transition: all .125s;
}

.thumb-group:focus, .thumb-group:hover {
  transition: all 1s;
  animation: .25s both animation_zoom;
  box-shadow: 0 0 .5em #fff;
}

.thumb-title {
  border: 0 solid var(--primary-color);
  z-index: 2;
  opacity: 0;
  padding-top: .075em;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--dark-text-color);
  pointer-events: none;
  background-color: #faebd7cc;
  border-top: none;
  border-radius: 0 0 1em 1em;
  width: 8em;
  height: 1.5em;
  margin-top: 4em;
  padding-inline-start: .75em;
  font-family: Ubuntu;
  font-size: .875em;
  position: absolute;
  overflow: hidden;
}

.animate:focus > .thumb-title, .animate:hover > .thumb-title {
  animation: .25s both animation_appear;
}

.thumbnail {
  border: .1em solid var(--primary-color);
  border-radius: .9em;
  width: 7.05em;
  height: 4.8em;
  margin-left: -.04em;
  position: relative;
}

#add_bookmark_button {
  background-color: var(--add-bookmark);
  -webkit-backdrop-filter: var(--blur-one-em);
  backdrop-filter: var(--blur-one-em);
  order: 1336;
}

#add_bookmark_button:not(:active) > i {
  transition: all .75s;
  rotate: 360deg;
}

#settings_button {
  color: var(--primary-color);
  -webkit-backdrop-filter: var(--blur-one-em);
  backdrop-filter: var(--blur-one-em);
  background-color: var(--settings);
  cursor: pointer;
  caret-color: #0000;
  border: 1px solid #ffffff38;
  border-radius: 50%;
  flex-direction: row;
  order: 1337;
  justify-content: center;
  align-items: center;
  width: 4em;
  height: 4em;
  margin: 2%;
  text-decoration: none;
  display: flex;
}

#settings_button * {
  margin: 0;
  padding: 0;
  font-size: 2em;
}

#settings_button:not(:active) > i {
  transition: all .75s;
  rotate: 360deg;
}

.custom_bookmark {
  color: var(--primary-color);
  -webkit-backdrop-filter: var(--blur-one-em);
  backdrop-filter: var(--blur-one-em);
  cursor: pointer;
  caret-color: #0000;
  background-color: var(--custom-bookmark);
  border: 1px solid #ffffff38;
  border-radius: 50%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 4em;
  height: 4em;
  margin: 2%;
  text-decoration: none;
  display: flex;
}

.custom_link_name {
  pointer-events: none;
  font-size: 1.5em;
  font-weight: 600;
}

.cross {
  display: var(--cross-display);
  background-color: var(--selection-color);
  border: 3px dashed tomato;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 4em;
  height: 4em;
  padding-top: 1em;
  padding-left: 2.5em;
  animation: 10s linear infinite spin;
  position: fixed;
}

#mydivheader {
  cursor: move;
  z-index: 4;
  color: #fff;
  background-color: #0000;
  padding: 0;
}

#import-bookmarks-btn, #import-backup-btn {
  display: none;
}

#advanced-settings-modal-background-overlay {
  -webkit-backdrop-filter: blur(3em);
  backdrop-filter: blur(3em);
  z-index: 2;
  background-color: #45454576;
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
}

#advanced-settings-modal {
  color: var(--white);
  z-index: 3;
  cursor: default;
  background-color: #38383800;
  width: 100dvw;
  height: auto;
  font-family: Ubuntu;
  display: none;
  position: absolute;
}

#btn-install {
  display: none;
}

.modal-page-title {
  background-color: var(--dark-low-alpha);
  border-radius: .5em;
  justify-content: flex-start;
  align-items: center;
  gap: .5em;
  margin: .5em;
  padding-block: .75em;
  padding-inline: .5em;
  font-size: 1.5em;
  font-weight: 700;
  display: flex;
}

.modal-close-btn {
  border-radius: 8px;
  padding-inline: .25em .3em;
  font-size: 1.4em;
  transition: all .125s;
}

.modal-close-btn:hover {
  background-color: #ffffff45;
  transform: scale(1.25);
}

.modal-close-btn:active {
  opacity: .5;
  transform: scale(1);
}

.modal-group {
  background-color: var(--dark-low-alpha);
  border-radius: .75em;
  flex-direction: column;
  margin: .75em;
  padding: .5em;
  display: flex;
  position: relative;
}

.modal-title {
  flex-direction: column;
  padding: .375em;
  font-size: 1.3em;
  font-weight: 700;
  display: flex;
}

.modal-option {
  border-radius: .375em;
  justify-content: space-between;
  padding-block: .5em;
  padding-inline: .5em;
  font-size: 1.1em;
  font-weight: 400;
  transition: all .125s;
  display: flex;
}

.modal-option:hover {
  background-color: var(--white-quarter-alpha);
}

.setting-preview {
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: end;
  color: var(--white-half-alpha);
  background-color: #06060400;
  border: 0 solid #000;
  border-radius: 6px;
  outline: 0;
  width: 10em;
  height: 1.25em;
  padding-inline-end: .5em;
  font-family: Ubuntu;
  font-size: .9em;
  animation-name: animatetop;
  animation-duration: 1s;
  overflow: hidden;
}

.version-preview {
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  color: var(--white-half-alpha);
  border-radius: 6px;
  width: auto;
  max-height: 1px;
  font-family: Ubuntu;
  font-size: .7em;
  animation-name: animatetop;
  animation-duration: 1s;
  overflow: hidden;
}

.setting-preview option {
  color: #060604;
  text-align: end;
  background-color: var(--dark-low-alpha);
}

.footer {
  z-index: 1;
  flex-direction: column;
  align-items: center;
  gap: .25em;
  width: 100%;
  padding: .5em;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.footer * {
  color: #ffffff80;
  gap: 1em;
  font-size: .875em;
  line-height: 1.5em;
  text-decoration: none;
  list-style: none;
  transition: all .15s;
  display: flex;
}

.footer :focus, .footer :hover {
  color: #fff;
}

.ext_favicon {
  border-radius: 50%;
  width: 32px;
  height: 32px;
}

.nested-close {
  opacity: 0;
  padding-block: 0;
  height: 0 !important;
}

.nested-one-element {
  height: 100%;
  transition: all .2s;
  overflow: hidden;
}

#deep-search-nested {
  height: 9.5em;
  transition: all .2s;
  overflow: hidden;
}

.loader {
  border-radius: 50%;
  animation: 1s infinite spin;
  border: 4px solid var(--primary-color) !important;
  border-top: 4px solid #1e90ff !important;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader * {
  animation: 1s infinite spin-rev;
}

@keyframes spin-rev {
  0% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0);
  }
}

.collapsed {
  height: 0;
